.rating {
  padding-top: 5px;
  user-select: none;
  cursor: pointer;

  //  .rating__star
  &__star {
    display: inline-block;
    width: 16px;
    height: 16px;
    transition: fill 0.3s ease 0s;
    &:not(:last-child) {
      padding-right: 5px;
    }

    svg path {
      fill-opacity: 1;
      fill: rgba(245, 173, 13, 1);
      transition: fill 0.3s ease 0s;
    }
  }
}
