.review {
  padding-bottom: 100px;
  @include big-desktop {
    padding-bottom: 0;
  }

  // .review__container

  &__container {
    margin: 0 auto;
    padding: 0 var(--container-offset);
    max-width: 1383px;
    @include tablet {
      padding: 0;
    }
  }

  // .review__swiper

  &__swiper {
    padding: 30px 75px 72px 78px;
    @include big-desktop {
      padding-left: 50px;
    }
    @include tablet {
      padding-left: 15px;
    }
    @include mobile-small {
      padding-right: 0px;
    }
  }
}

.review-slide {
  background-color: var(--light-color);
  box-shadow: 0px 10px 60px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  font-family: var(--font-family);
  padding: 51px 30px 43px 38px;
  box-sizing: border-box;
  margin-right: 30px;
  position: relative;
  z-index: 35;
  &:nth-child(4n) {
    opacity: 0;
    transition: all 0.4s ease 0s;
  }
  &__persone {
    column-gap: 17px;
    align-items: center;
    margin-bottom: 22px;
  }

  // .review-slide__img

  &__img {
  }

  // .review-slide__info

  &__info {
    flex-direction: column;
  }

  // .review-slide__name

  &__name {
    font-size: 16px;
    line-height: 138%;
    color: var(--link-color);
  }

  // .review-slide__work

  &__work {
    font-size: 15px;
    line-height: 147%;
    color: #acacac;
  }

  // .review-slide__text

  &__text {
  }

  // .review-slide__title

  &__title {
    font-size: 18px;
    line-height: 122%;
    margin-bottom: 20px;
    position: relative;
    display: inline-flex;
    column-gap: 8px;
    align-items: center;
    &::after {
      content: "";
      width: 16px;
      height: 16px;
      display: inline-block;
      background-repeat: no-repeat;
    }
    &--love {
      &::after {
        background-image: url("/img/svg/love.svg");
      }
    }
    &--cup {
      &::after {
        background-image: url("/img/svg/cup.svg");
      }
    }
  }

  // .review-slide__description

  &__description {
    margin: 0;
    font-size: 15px;
    line-height: 160%;
    color: var(--content-color);
    margin-bottom: 28px;
  }

  // .review-slide__score

  &__score {
    font-size: 17px;
    line-height: 141%;
    color: #f5ad0d;
    margin-right: 15px;
  }
}

.swiper-pagination-bullet {
  background: rgba(0, 214, 100, 0.1);
  opacity: 1;
  width: 10px;
  height: 10px;
  margin-right: 20px;
  --swiper-pagination-bullet-horizontal-gap: 10px;
}
.swiper-pagination-bullet-active {
  background-color: var(--link-color);
}

.swiper-button-next,
.swiper-button-prev {
  width: 45px;
  height: 45px;
  background-color: rgba(0, 214, 100, 0.1);
  border-radius: 50%;
  top: 45.5%;
  transition: all 0.5s ease 0s;
  &::after {
    width: 15px;
    height: 15px;
    content: "";
    background-image: url("/img/svg/arrow.svg");
  }
  @media (any-hover: hover) {
    &:hover {
      background-color: var(--header-color);
    }
  }
  @include tablet {
    display: none;
  }
}
.swiper-button-prev {
  left: 0;
  &::after {
    transform: rotate(180deg);
  }
}
.swiper-button-next {
  right: 0;
}
