.map {
  position: relative;
  // .map__info
  iframe {
    width: 100%;
  }
  &__info {
  }
}

.map-info {
  padding: 65px;
  position: absolute;
  background-color: var(--light-color);
  top: 16%;
  right: 18.8%;
  box-sizing: border-box;
  max-width: 550px;
  width: 28.5%;
  @include big-desktop {
    width: 50%;
  }
  @include tablet {
    width: 70%;
  }
  @include small-tablet {
    right: 15px;
    width: 85%;
  }
  @include mobile {
    top: 5%;
    padding-left: 15px;
    padding-right: 15px;
  }

  &--open {
    position: absolute;
    right: 5%;
    top: 5%;
    width: 30px;
    height: 30px;
    background-color: var(--link-color);
    border-radius: 50%;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    animation: infinityBounce 1.7s ease 1;
    transition: all 0.3s ease 0s;

    @media (any-hover: hover) {
      &:hover {
        background-color: var(--header-color);
        .map-info--open-arrow {
          &:before,
          &:after {
            border-color: var(--link-color);
          }
        }
      }
    }

    &-arrow {
      width: 10px;
      height: 10px;
      display: inline-block;
      left: 50%;
      top: 50%;
      transform: rotate(135deg);

      &::before,
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        border-style: solid;
        border-color: var(--light-color);
        transition: all 0.3s ease 0s;
      }

      &::before {
        content: "";
        display: block;
        border-width: 3px 3px 0 0;
        border-style: solid;
      }

      &:after {
        content: "";
        float: left;
        position: relative;
        top: -100%;
        border-width: 0 3px 0 0;
      }
    }
  }
  // .map-info__content

  &__content {
    padding-left: 80px;
    position: relative;

    &:before {
      content: "";
      display: block;
      position: absolute;
      width: 50px;
      height: 50px;
      left: 9px;
      background-repeat: no-repeat;
      background-position: center;
    }

    &--mark {
      &:before {
        background-image: url("../img/mark.png");
      }
    }

    &--phone {
      &:before {
        background-image: url("../img/phone.png");
      }
    }

    &--clock {
      &:before {
        background-image: url("../img/clock.png");
      }
    }
  }

  // .map-info__wrapper

  &__wrapper {
    flex-direction: column;
    row-gap: 50px;
  }

  // .map-info__text

  &__text {
  }

  // .map-info__title

  &__title {
    font-size: 16px;
    letter-spacing: 0.01em;
    margin-bottom: 24px;
    &--tel {
      letter-spacing: 0.02em;
      font-size: 17px;
    }
  }

  // .map-info__description

  &__description {
    margin: 0;
    letter-spacing: -0.01em;
    flex-direction: column;
    row-gap: 5px;
    &--phone {
      display: flex;
      flex-direction: column;
      column-gap: 5px;
    }
  }

  // .map-info__opening

  &__opening {
    justify-content: space-between;
  }

  // .map-info__description--contact

  &__description-contact {
  }

  // .map-info__day

  &__day {
    flex-direction: column;
    row-gap: 13px;
  }

  // .map-info__time

  &__time {
    flex-direction: column;
    row-gap: 13px;
  }

  // .map-info__close

  &__close {
    position: absolute;
    top: 13px;
    right: 20px;
    width: 30px;
    height: 30px;
    border: 1px solid rgba(30, 38, 109, 0.5);
    transition: all 0.3s ease 0s;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 9px;
      height: 2px;
      background-color: var(--header-color);
      top: 50%;
      left: 50%;
      transition: all 0.3s ease 0s;
    }
    &::before {
      transform: translate(-50%, -50%) rotate(45deg);
    }
    &::after {
      transform: translate(-50%, -50%) rotate(-45deg);
    }
    @media (any-hover: hover) {
      &:hover {
        background-color: var(--link-color);
        transition: all 0.3s ease 0s;
        &::before,
        &::after {
          content: "";
          background-color: var(--light-color);
          transition: all 0.3s ease 0s;
        }
      }
    }
  }
  // .map-info__offset
  &__offset {
    margin-bottom: 20px;
  }
}
