.footer {
  // .footer__container

  &__container {
    padding: 88px 53px 40px 50px;
    flex-direction: column;
    @include tablet {
      padding: 50px 20px 50px 20px;
    }
  }
}
.footer-menu {
  justify-content: space-between;
  margin-bottom: 123px;
  gap: 30px;
  @include tablet {
    margin-bottom: 50px;
    flex-wrap: wrap;
  }
  @include mobile-small {
    justify-content: center;
  }

  // .footer-menu__list

  &__list {
    gap: 26px;
    @include tablet {
      flex-wrap: wrap;
    }
    @include small-tablet {
      flex: 1 1 100%;
      justify-content: center;
    }
  }

  // .footer-menu__item

  &__item {
  }

  // .footer-menu__link

  &__link {
    font-family: var(--font-family-rm);
    font-size: 16px;
    line-height: 100%;
    letter-spacing: 0.01em;
    color: var(--content-color);
    background-image: linear-gradient(to right, var(--link-color), var(--link-color) 50%, var(--content-color) 50%);
  }

  // .footer-menu__social

  &__social {
    gap: 24px;
    margin-right: 3px;
    @include small-tablet {
      flex: 1 1 100%;
      justify-content: center;
    }
  }
}
.footer-links {
  &:not(:last-child) {
    min-width: 146px;
    margin-right: 98px;
  }
  &:first-child {
    margin-right: 113px;
  }
  @include big-desktop {
    &:not(:last-child),
    &:first-child {
      margin-right: 35px;
    }
  }
  @include small-tablet {
    flex: 0 0 48%;
    &:not(:last-child),
    &:first-child {
      margin-right: 0;
      text-align: center;
    }
  }

  // .footer-links__title

  &__title {
    margin: 0;
    font-family: var(--font-family-fp);
    font-weight: 450;
    font-size: 18px;
    line-height: 89%;
    letter-spacing: 0.01em;
    color: #39374d;
    margin-bottom: 18px;
  }

  // .footer-links__contact

  &__contact {
    text-align: right;
    flex-grow: 1;
    @include tablet {
      flex: 1 1 100%;
      text-align: center;
    }
  }

  // .footer-links__contact-title

  &__contact-title {
    margin-bottom: 29px;
  }

  // .footer-links__wrapper
  &__wrapper {
    margin-bottom: 95px;
    @include tablet {
      row-gap: 50px;
      flex-wrap: wrap;
      justify-content: center;
    }
    @include small-tablet {
      column-gap: 20px;
    }
  }

  // .footer-links__list

  &__list {
  }

  // .footer-links__item

  &__item {
  }

  // .footer-links__link

  &__link {
    font-family: var(--font-family-rm);
    font-weight: 500;
    font-size: 18px;
    line-height: 250%;
    letter-spacing: -0.04em;
    background-image: linear-gradient(to right, var(--link-color), var(--link-color) 50%, var(--content-color) 50%);
  }
  // .footer-links__address

  &__address {
    font-family: var(--font-family-rr);
    font-size: 16px;
    line-height: 150%;
    text-align: right;
    align-self: flex-end;
    display: block;
    letter-spacing: 0.01em;
    color: var(--content-color);

    @include tablet {
      text-align: center;
    }
  }
  // .footer-links__address-tel

  &__address-tel {
    display: inline;
    background-image: linear-gradient(to right, var(--link-color), var(--link-color) 50%, var(--content-color) 50%);
  }
}

.footer-keydesign {
  font-family: var(--font-family-rr);
  font-size: 16px;
  line-height: 150%;
  color: var(--content-color);
  max-width: 854px;
  justify-content: space-between;
  // .footer-keydesign__sways

  &__sways {
    margin: 0;
  }

  // .footer-keydesign__privacy

  &__privacy {
    margin: 0;
    color: #39374d;
  }
}

.social {
  // .social__item

  &__item {
  }

  // .social__link

  &__link {
    svg path {
      transition: all 0.3s ease 0s;
    }

    @media (any-hover: hover) {
      &:hover {
        svg path {
          fill: var(--link-color);
          transition: all 0.3s ease 0s;
        }
      }
    }
  }

  // .social__link--fb

  &__link--fb {
  }

  // .social__link--insta

  &__link--insta {
  }

  // .social__link--tw

  &__link--tw {
  }
}
