.header {
  padding: 38px 0px;

  @include mobile-small {
    padding: 20px 0px;
  }

  // .header__logo

  &__logo {
    display: inline-block;
    min-width: 71px;
    svg path {
      transition: all 0.3s ease 0s;
    }
    @media (any-hover: hover) {
      &:hover {
        svg path {
          fill: var(--link-color);
          transform: translateY(-2px);
          transition: all 0.3s ease 0s;
        }
      }
    }
    &:active {
      svg {
        opacity: 0.7;
        transition: all 0.3s ease 0s;
        transform: translateY(2px);
      }
    }
  }

  // .header__menu

  &__menu {
    column-gap: 22px;

    @include small-tablet {
      order: 3;
    }
  }

  // .header__order

  &__order {
    padding: 13px 34px;
  }

  // .header__container

  &__container {
    align-items: center;
    padding: 0px 36px;
    column-gap: 25px;

    @include tablet {
      padding: 0px 15px;
      justify-content: space-between;
    }
  }

  &--position {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
}

.menu {
  min-height: 51px;
  flex: 1 1 auto;
  display: flex;
  justify-content: center;
  align-items: center;

  @include small-tablet {
    flex-grow: 0;
    justify-content: flex-end;
  }

  // .menu__body

  &__body {
    @include small-tablet {
      position: fixed;
      left: 0;
      top: 0;
      width: 68%;
      height: 100vh;
      overflow-y: auto;
      z-index: 6;
      visibility: hidden;
      background-color: var(--header-color);
      transform: translateX(-100%);
      transition: transform 0.3s ease 0s, visibility 0.3s ease 0s;
    }
  }

  // .menu__list
  &__list {
    column-gap: 28px;
    flex-wrap: wrap;
    justify-content: center;
    row-gap: 10px;
    margin-right: 7px;

    @include small-tablet {
      flex-direction: column;
      align-items: center;
      padding: 80px 40px;
      row-gap: 40px;
    }
  }

  // .menu__item

  &__item {
  }

  // .menu__link

  &__link {
    line-height: 106%;
    color: var(--light-color);
    position: relative;
    @include small-tablet {
      font-size: 25px;
      word-break: break-word;

      &::after {
        display: none;
      }
    }
  }

  // .menu__services

  &__services {
    position: relative;

    @include small-tablet {
      order: -1;
    }

    @include mobile {
      display: none;
    }
  }
}

.services {
  align-items: center;
  column-gap: 30px;

  @include tablet {
    column-gap: 15px;
  }

  // .services__link

  &__link {
    width: 17px;
    height: 17px;

    svg path {
      transition: all 0.3s ease 0s;
    }

    @media (any-hover: hover) {
      &:hover {
        svg path {
          fill: var(--link-color);
        }
      }
    }
  }

  // .services__link-lang

  &__link-lang {
    background: url("../img/svg/flag.svg") 0 0 no-repeat;
    width: 16px;
    height: 11px;
  }

  // .services__link-basket

  &__link-basket {
  }

  // .services__search

  &__search {
  }
}

.menu__body.menu--active {
  transform: none;

  @include small-tablet {
    visibility: visible;
    // transform: none;

    overflow-y: auto;
  }
}

.link--animation {
  background-image: linear-gradient(to right, var(--link-color), var(--link-color) 50%, #fff 50%);
  background-size: 200% 100%;
  background-position: -100%;
  display: inline-block;
  position: relative;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all 0.3s ease-in-out;
  &:active {
    transform: translateY(2px);
    // display: none;
    // rgba(0, 214, 100, 0.9)
  }

  &::before {
    content: "";
    background-color: var(--link-color);
    display: block;
    position: absolute;
    bottom: -3px;
    left: 0;
    width: 0;
    height: 3px;
    transition: all 0.3s ease-in-out;
  }

  @media (any-hover: hover) {
    &:hover {
      background-position: 0;
      .link__arrow {
        svg path {
          fill: var(--link-color);
        }
      }
      &::before {
        width: 100%;
      }
    }
  }
}

.search {
  display: none;
  width: 180px;
  position: absolute;
  right: -10px;
  top: 35px;
  background-color: var(--light-color);
  z-index: 6;
  padding: 10px;
  border-radius: 20px;
  border: 1px solid var(--header-color);

  // .search--active

  &--active {
    display: block;
  }
}

.search-form {
  position: relative;
  // .search-form__label

  &__label {
  }

  // .search-form__input

  &__input {
    padding-left: 10px;
    padding-right: 37px;
    width: 100%;
    height: 26px;
    border-radius: 10px;
    position: relative;
    z-index: 1;

    border: none;
    background-color: #f0f0f0;

    &:focus {
      border: 0;
      outline: 0;
    }
  }

  // .search-form__btn

  &__btn {
    width: 20px;
    height: 20px;
    position: absolute;
    right: 0;
    top: 3px;
    z-index: 4;
    // padding: 5px;
  }
}
