.btn {
  text-align: center;
  color: var(--light-color);
  background: #00d664;
  border-radius: 27.5px;
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 27px;
  white-space: nowrap;
  display: inline-flex;
}

.title {
  margin: 0;
  font-family: var(--font-family);
  color: var(--header-color);
}

.btn__arrow {
  display: block;
  background-image: url("../img/svg/menu-arrow.svg");
  width: 9px;
  height: 6px;
  transform: rotate(-90deg);
  margin-top: 4px;
}

.opacity {
  opacity: 1;
  transition: all 0.3s ease 0s;
}
