.catering {
  padding-top: 100px;
  padding-bottom: 100px;
  @include tablet {
    padding-bottom: 50px;
    padding-top: 50px;
  }

  // .catering__container

  &__container {
  }

  // .catering__title

  &__title {
    color: var(--header-color);
    font-size: 42px;
    @include small-tablet {
      font-size: 30px;
    }
  }

  // .catering__subtitle

  &__subtitle {
    color: #acacac;
    font-size: 42px;
  }

  // .catering__description

  &__description {
    color: var(--content-color);
    max-width: 800px;
  }

  // .catering__content

  &__content {
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-items: center;
    grid-column: span 12;
  }

  // .catering__image

  &__image {
  }
}

.advantages {
  grid-column: span 12;
  padding: 0px 24px;
  align-items: center;

  @include big-desktop {
    padding: 0;
  }
  @include tablet {
    flex-wrap: wrap;
  }

  // .advantages__wrapper

  &__wrapper {
    flex-direction: column;
    flex: 1 1 37%;
    align-self: flex-end;
    margin-bottom: 65px;
    @include tablet {
      margin-bottom: 0;
    }
  }

  // .advantages__title

  &__title {
    font-size: 28px;
    line-height: 129%;
    margin-bottom: 50px;
    @include tablet {
      text-align: center;
      font-size: 24px;
    }
  }

  // .advantages__list

  &__list {
  }

  // .advantages__item

  &__item {
    column-gap: 34px;
    margin-bottom: 28px;
    position: relative;

    &::before {
      content: "";
      display: inline-block;
      min-width: 40px;
      height: 40px;
      background-repeat: no-repeat;
      background-position: center;
      margin-top: 10px;
    }
    &--shrimp {
      &::before {
        background-image: url("../img/catering-icon1.png");
      }
    }
    &--pepper {
      &::before {
        background-image: url("../img/catering-icon2.png");
      }
    }
    &--pan {
      &::before {
        background-image: url("../img/catering-icon3.png");
      }
    }
    @include tablet {
      justify-content: center;
    }
  }

  // .advantages__content

  &__content {
    flex-direction: column;
    row-gap: 10px;
  }
  // .advantages__subtitle

  &__subtitle {
    font-family: var(--font-family);
    font-size: 20px;
    line-height: 220%;
  }

  // .advantages__description

  &__description {
    margin: 0;
    font-family: var(--font-family-reg);
    font-size: 18px;
    line-height: 156%;
    letter-spacing: -0.01em;
    color: var(--content-color);
    max-width: 415px;
  }

  // .advantages__image

  &__image {
    flex: 1 1 auto;
    display: flex;
    justify-content: flex-end;
    @include tablet {
      justify-content: center;
    }
  }
}
