.checkout {
  background: url("../img/Menu-Card.png") left center / cover fixed no-repeat;
  min-height: 850px;

  @media only screen and (max-width: 1024px) {
    background: url("../img/Menu-CardTablet.png") left center / cover fixed no-repeat;
  }

  @media only screen and (max-width: 576px) {
    background: url("../img/previewMobile.png") right center / cover fixed no-repeat;
  }

  @media only screen and (min-resolution: 2dppx) and (min-resolution: 192dpi) and (min-width: 1440px) {
    background: url("../img/Menu-Cardx2.png") center center / cover fixed no-repeat;
  }

  // .checkout__container

  &__container {
    padding: 129px 20px 103px 20px;

    @include tablet {
      padding: 75px 15px 103px 15px;
    }

    @include mobile-small {
      padding: 50px 15px 50px 15px;
    }
  }

  // .checkout__title

  &__title {
    grid-column: 3 / span 8;
    font-size: 34px;
    line-height: 44px;
    text-align: center;
    letter-spacing: 0.01em;
    color: var(--light-color);
    margin-bottom: 76px;

    @include tablet {
      grid-column: span 12;
      font-size: 30px;
      margin-bottom: 40px;
    }
  }

  // .checkout__content

  &__content {
    grid-column: span 12;
    background-color: var(--light-color);
    border-radius: 10px;
    padding: 4px 110px 70px 98px;

    @include big-desktop {
      padding: 4px 55px 35px 49px;
    }

    @include tablet {
      padding: 4px 20px 35px 20px;
    }
  }

  // .checkout__tabs

  &__tabs {
    justify-content: center;
    margin-bottom: 77px;

    @include big-desktop {
      margin-bottom: 27px;
    }
  }

  // .checkout__list

  &__list {
    grid-template-columns: repeat(2, 1fr);
    row-gap: 22px;
    column-gap: 40px;
    @include tablet {
      grid-template-columns: 1fr;
    }
  }

  // .checkout__item

  &__item {
  }
}

.tabs {
  // .tabs__list

  &__list {
    column-gap: 33px;
    row-gap: 22px;

    @include mobile-small {
      column-gap: 15px;
    }
  }

  // .tabs__item

  &__item {
  }

  // .tabs__title

  &__title {
    font-family: var(--font-family);
    color: #777777;
    font-size: 16px;
    letter-spacing: -0.01em;
    line-height: 150%;
    padding: 23px 23px 23px 23px;

    @media (any-hover: hover) {
      &:hover {
        transition: all 0.3s ease 0s;
      }
    }

    // .tabs__title--active
    &--active {
      color: var(--header-color);
      position: relative;
      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 18%;
        width: 64%;
        height: 2px;
        background: #00d664;
        animation: fade-border 0.3s;
      }
    }
  }
}

.kit {
  column-gap: 18px;

  @include mobile-small {
    align-items: center;
    justify-content: center;
    row-gap: 10px;
    flex-direction: column;
  }

  // .kit__content

  &__content {
    flex: 1 1 auto;
    flex-direction: column;
    justify-content: space-between;

    @include tablet {
      justify-content: flex-start;
      row-gap: 8px;
    }

    @include mobile-small {
      align-items: center;
    }

    // overflow: hidden;
  }

  // .kit__title

  &__title {
    font-size: 22px;
    line-height: 109%;
    letter-spacing: -0.01em;
    position: relative;
    display: inline-flex;
    z-index: 2;
    column-gap: 2px;

    &::after {
      content: "";
      display: block;
      border-bottom: 1px dashed rgba(105, 105, 105, 0.6);
      flex: 1 1 auto;
    }

    @include tablet {
      font-size: 18px;
    }

    @include mobile-small {
      &::after {
        display: none;
      }
    }
  }

  // .kit__description

  &__description {
    margin: 0;
    font-family: var(--font-family-rr);
    font-size: 16px;
    line-height: 150%;
    letter-spacing: -0.01em;
    color: var(--content-color);

    @include tablet {
      font-size: 14px;
    }
  }

  // .kit__image

  &__image {
    order: -1;
  }

  // .kit__price

  &__price {
    font-family: var(--font-family-reg);
    font-size: 22px;
    line-height: 118%;
    text-align: center;
    letter-spacing: -0.01em;
    color: #121212;
  }
}
