// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "NeueHaasUnicaMedium", sans-serif;
  --font-family-reg: "NeueHaasUnicaRegular", sans-serif;
  --font-family-fp: "futuraPtMedium", sans-serif;
  --font-family-mrs: "MrsSaintDelafieldRegular";
  --font-family-rm: "RobotoMedium", sans-serif;
  --font-family-rr: "RobotoRegular", sans-serif;
  --content-width: 1296px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --header-color: #1e266d;
  --link-color: #00d664;
  --content-color: #696969;
  --content-color-grey: #7c7c7c;
}
