.fullservice {
  min-height: 813px;
  background-color: #f7f4fb;
  padding: 165px 0 100px 0;

  @include tablet {
    padding: 50px 0 50px 0;
  }
  // .fullservice__container

  &__container {
  }

  // .fullservice__content

  &__content {
    padding: 0px 30px;
    flex-direction: column;
    align-items: center;
    @include tablet {
      padding: 0;
    }
  }

  // .fullservice__header

  &__header {
    font-size: 34px;
    line-height: 171%;
    text-align: center;
    margin-bottom: 72px;
    @include tablet {
      margin-bottom: 30px;
    }
  }

  // .fullservice__list

  &__list {
    gap: 30px;
    margin-bottom: 76px;
    flex: 1 1 25%;
    flex-wrap: wrap;
    justify-content: center;
  }

  // .fullservice__item

  &__item {
    background-color: var(--light-color);
    border-radius: 5px;
    flex-direction: column;
    padding: 40px;
    max-width: 286px;
    position: relative;
    z-index: 2;

    &:before {
      content: "";
      position: absolute;
      overflow: visible;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: 100%;
      transition: all 0.6s ease-out 0s;
      box-shadow: 0px 10px 60px rgba(0, 0, 0, 0.1);
    }

    &:last-child {
      &:after {
        content: "";
        position: absolute;
        right: -50px;
        bottom: 3px;
        max-width: 45px;
        max-height: 147px;
        width: 100%;
        height: 100%;
        background-image: url("../img/dots.svg");
        background-repeat: no-repeat;
      }
    }
    @media (any-hover: hover) {
      &:hover {
        &::before {
          transition: all 0.6s ease 0s;
          box-shadow: 0.8px 0.9px 3px grey;
        }
      }
    }
  }

  // .fullservice__title

  &__title {
    font-size: 20px;
    line-height: 220%;
    text-align: center;
    letter-spacing: -0.01em;
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 10px;
    &::before {
      flex: 1 1 100%;
      content: "";
      display: block;
      background-repeat: no-repeat;
      background-position: center;
    }
    &--anniversaries {
      &::before {
        background-image: url("../img/anniversaries.png");
        width: 59px;
        height: 56px;
      }
    }
    &--corporate {
      &::before {
        background-image: url("../img/corporate.png");
        width: 52px;
        height: 48px;
      }
    }

    &--weddings {
      &::before {
        background-image: url("../img/weddings.png");
        width: 64px;
        height: 51px;
      }
    }
    &--social {
      &::before {
        background-image: url("../img/social.png");
        width: 48px;
        height: 57px;
      }
    }
  }

  // .fullservice__subtitle

  &__subtitle {
    margin: 0;
    font-family: var(--font-family-reg);
    font-size: 15px;
    line-height: 160%;
    text-align: center;
    letter-spacing: -0.01em;
    color: var(--content-color);
    margin-bottom: 27px;
  }

  // .fullservice__link

  &__link {
    display: block;
    font-family: var(--font-family);
    font-size: 15px;
    line-height: 160%;
    letter-spacing: -0.01em;
    color: var(--link-color);
    text-align: center;
  }

  // .fullservice__btn

  &__btn {
    padding: 14px 30px;
    align-items: center;
    column-gap: 9px;
  }
}
