.subscribe {
  padding: 106px 20px 87px 20px;
  @include big-desktop {
    padding: 50px 0px 50px 0px;
  }
  // .subscribe__container

  &__container {
    row-gap: 50px;
  }

  // .subscribe__title

  &__title {
    grid-column: 4 span;
    font-size: 28px;
    line-height: 129%;
    letter-spacing: -0.01em;
    color: #39374d;
    text-align: center;
    padding-left: 16px;
    @include big-desktop {
      grid-column: 12 span;
      padding-left: 0;
    }
  }

  // .subscribe__form

  &__form {
    grid-column: 7 / 6 span;
    column-gap: 6px;
    @include big-desktop {
      justify-content: center;
      grid-column: 12 span;
    }
    @include mobile {
      flex-wrap: wrap;
      row-gap: 30px;
    }
  }
}

.form-subscribe {
  // .form-subscribe__label

  &__label {
  }
  // .form-subscribe__label-checkbox

  &__label-checkbox {
    transform: translateX(8px);
    column-gap: 8px;
  }
  // .form-subscribe__wrapper

  &__wrapper {
    flex-direction: column;
    row-gap: 16px;
  }

  // .form-subscribe__input

  &__input {
    flex-grow: 1;
    padding: 16px 20px 16px 20px;
    font-family: var(--font-family);
    font-size: 15px;
    line-height: 100%;
    letter-spacing: -0.01em;
    color: var(--header-color);
    background: #edfcf4;
    border: 1px solid #f3f3f3;
    border-radius: 30px;
    outline: 1px solid var(--header-color);
    &::placeholder {
      font-family: var(--font-family);
      font-size: 15px;
      line-height: 100%;
      letter-spacing: -0.01em;
      color: var(--header-color);
    }
  }

  // .form-subscribe__checkbox

  &__checkbox {
    &:checked {
      + .custom-checkbox__content {
        &::after {
          display: block;
        }
      }
    }
  }
  // .form-subscribe__btn

  &__btn {
    padding: 17px 43.5px;
    align-self: flex-start;
  }
}

.custom-checkbox {
  // .custom-checkbox__content

  &__content {
    display: inline-block;
    position: relative;
    width: 13px;
    height: 13px;
    border: 1px solid #b6b6b6;
    border-radius: 2px;
    &::after {
      content: "";
      display: none;
      position: absolute;
      width: 13px;
      height: 13px;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      background-image: url("../img/svg/checkbox.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  // .custom-checkbox__text

  &__text {
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 107%;
    text-align: center;
    letter-spacing: -0.01em;
    color: #acacac;
  }
}
