.preview {
  background: url("../img/preview.png") center center / cover no-repeat;
  height: var(--vh);

  @media only screen and (max-width: 1024px) {
    background: url("../img/previewTablet.png") center center / cover no-repeat;
  }

  @media only screen and (max-width: 576px) {
    background: url("..//img/previewMobile.png") center center / cover no-repeat;
  }

  @media only screen and (min-resolution: 2dppx) and (min-resolution: 192dpi) and (min-width: 1440px) {
    background: url("../img/preview2x.png") center center / cover no-repeat;
  }

  @include tablet {
    height: auto;
  }

  // .preview__container
  &__container {
  }

  // .preview__content

  &__content {
    color: var(--light-color);
    flex-direction: column;
    grid-column: 3 / span 8;
    align-items: center;
    padding-top: 47.5%;
    padding-bottom: 288px;
    text-align: center;
    min-height: 400px;

    @media (min-width: 2000px) {
      padding: 0;
      align-items: center;
      align-content: center;
      justify-content: center;
    }

    @include tablet {
      grid-column: 12 span;
      padding-top: 300px;
      padding-bottom: 150px;
    }

    @include small-tablet {
      padding-top: 200px;
      padding-bottom: 140px;
    }

    @include mobile-small {
      padding-top: 150px;
      padding-bottom: 50px;
    }
  }

  // .preview__title

  &__title {
    margin: 0 0 40px 0;
    font-family: var(--font-family);
    font-size: 60px;
    line-height: 117%;
    letter-spacing: 0.02em;

    @include small-tablet {
      font-size: 48px;
    }

    @include mobile {
      font-size: 36px;
    }
  }

  // .preview__subtitle

  &__subtitle {
    margin: 0 0 20px 0;
    order: -1;
    display: inline-block;
    font-family: var(--font-family-mrs);
    font-size: 58px;
    line-height: 29%;

    @include small-tablet {
      font-size: 34px;
    }
  }

  // .preview__description

  &__description {
    font-size: 18px;
    line-height: 150%;
    font-family: var(--font-family-reg);
    letter-spacing: 0.02em;
    margin-bottom: 65px;
    max-width: 610px;

    @include tablet {
      margin-bottom: 30px;
      max-width: 100%;
    }
  }

  // .preview__btn

  &__btn {
    font-family: var(--font-family-rm);
    font-size: 18px;
    padding: 17px 40px;
    border-radius: 30px;
    min-height: 55px;
    letter-spacing: 0.02em;
  }
}
