.store {
  padding-top: 124px;
  padding-bottom: 66px;

  @include tablet {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  // .store__container

  &__container {
  }

  // .store__title

  &__title {
    grid-column: 4 / span 7;
    font-size: 34px;
    line-height: 129%;
    text-align: center;
    margin-bottom: 80px;

    @include tablet {
      margin-bottom: 40px;
    }

    @include mobile {
      grid-column: 12 span;
      font-size: 28px;
    }
  }

  // .store__content

  &__content {
    grid-column: span 12;
    margin-bottom: 73px;

    @include tablet {
      margin-bottom: 30px;
    }
  }

  // .store__list

  &__list {
    grid-template-columns: repeat(auto-fit, minmax(289px, 1fr));
    column-gap: 31px;
    padding: 0px 21px;
    row-gap: 35px;
    justify-items: center;

    @include mobile {
      padding: 0;
    }

    @include big-desktop {
      padding: 0;
    }
  }

  // .store__item

  &__item {
    max-width: 289px;
    flex-direction: column;
  }

  // .store__btn

  &__btn {
    display: block;
    grid-column: span 12;
    margin: 0 auto;
    position: relative;
    padding: 12px 30px 14px 26px;
    display: inline-flex;
    align-items: center;
    column-gap: 21px;
    font-size: 18px;
    line-height: 150%;
  }
}

.store-item {
  // .store-item__title

  &__title {
    margin: 0;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: -0.01em;
    padding-top: 18px;
    margin-bottom: 15px;
  }

  // .store-item__wrapper

  &__wrapper {
    justify-content: space-between;
    flex-wrap: wrap;
  }

  // .store-item__price

  &__price {
    font-family: var(--font-family-reg);
    font-size: 18px;
    line-height: 100%;
    letter-spacing: -0.01em;
    color: var(--content-color);
  }

  // .store-item__img

  &__img {
    position: relative;
    order: -1;
    height: 289px;
    width: 289px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 32px;
  }

  // .store-item__img--sale
  &__img--sale {
    position: absolute;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    width: 83px;
    height: 29px;
    right: 15px;
    top: 15px;
    background-color: #00d664;
    border-radius: 5px;
    font-family: var(--font-family-reg);
    color: var(--light-color);
    font-size: 14px;
    line-height: 129%;
    letter-spacing: -0.01em;
    text-align: center;
    transition: all 0.3s ease 0s;
  }

  // .store-item__image

  &__image {
    width: 100%;
    height: 100%;
    display: inline-block;
    transition: 0.3s ease-in-out;
    @media (any-hover: hover) {
      &:hover {
        box-shadow: 0 12px 28px rgba(0, 0, 0, 0.4);
        transform: scale(1.1);
        &:before {
          opacity: 1;
        }
        + .store-item__img--sale {
          transform: scale(1.05);
        }
      }
    }
  }
}
