.important {
  padding-top: 100px;
  padding-bottom: 100px;
  background-color: #f5f6fe;
  @include tablet {
    padding-top: 50px;
    padding-bottom: 50px;
  }

  // .important__container

  &__container {
    align-items: center;
    column-gap: 120px;
    @include big-desktop {
      column-gap: 30px;
    }
    @include tablet {
      column-gap: 30px;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
    }
  }

  // .important__content

  &__content {
    padding-top: 37px;
    flex: 1 1 50%;
    @include big-desktop {
      display: flex;
      align-items: center;
      flex-direction: column;
    }
  }

  // .important__title

  &__title {
    font-size: 34px;
    line-height: 129%;
    letter-spacing: -0.01em;
    margin-bottom: 25px;
    @include tablet {
      text-align: center;
      font-size: 28px;
    }
  }

  // .important__description

  &__description {
    margin: 0;
    font-family: var(--font-family-reg);
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.01em;
    color: #7c7c7c;
    margin-bottom: 63px;
    padding-right: 70px;
    @include tablet {
      padding: 0;
      text-align: center;
      margin-bottom: 30px;
    }
  }

  // .important__video

  &__video {
    width: 578px;
    height: 424px;
    flex: 1 1 50%;
    order: -1;
    position: relative;

    &::after {
      content: "";
      background-image: url("../img/svg/dots.svg");
      background-repeat: no-repeat;
      position: absolute;
      right: -30px;
      bottom: -53px;
      width: 216px;
      height: 159px;
      z-index: 1;
    }
    @include big-desktop {
      flex: 0 1 auto;
    }
  }

  // .important__img

  &__img {
    position: relative;
    z-index: 2;
  }

  // .important__btn

  &__btn {
    position: relative;
    padding: 12px 30px 14px 26px;
    display: inline-flex;
    align-items: center;
    column-gap: 24px;
  }
}
