// !fade
.fade {
  animation-name: fade;
  animation-duration: 1.25s;
}
.showFade {
  display: grid;
}
.hideFade {
  display: none;
}

.fade-fast {
  animation-name: fade;
  animation-duration: 0.7s;
}

@keyframes fade {
  0% {
    opacity: 0.1;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-border {
  0% {
    width: 0;
  }
  100% {
    width: 64%;
  }
}
